/* "inspired" by https://developer.mozilla.org/fr/docs/Web/CSS/CSS_Grid_Layout */
* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	text-align: center;
}
ul {
    background-color: #dddddd;
    list-style-type: none;
}
li {
    float: left;
    border: 1px;
    border-style: dotted;
}

a {
    display: block;
    padding: 8px;
    background-color: #dddddd;
}
.testActive {
  background-color: red;
}
.wrapper {
  max-width: 940px;
  margin: 0 auto;
}
.wrapper > div {
  border: 2px solid rgb(233,171,88);
  border-radius: 5px;
  background-color: rgba(233,171,88,.5);
  padding: 1em;
  color: #d9480f;
}
.wrapper {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  grid-gap: 10px;
  grid-auto-rows: minmax(100px, auto);
}
.todoItem > div {
  border: 2px solid rgb(233,171,88);
  border-radius: 5px;
  background-color: rgba(233,171,88,.5);
  padding: 1em;
  color: #d9480f;
}
.todoItem {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-gap: 10px;
  grid-auto-rows: minmax(100px, auto);
}

.todoItem .id {
  grid-column: 1 / 2;
  grid-row: 1;
}
.todoItem .text{
  grid-column: 2 / 8;
  grid-row: 1;
}
.todoItem .buttons {
  grid-column: 8 / 9;
  grid-row: 1;
}

.button_wrapper > div {
  border: 2px solid rgb(233,171,88);
  border-radius: 5px;
  background-color: rgba(233,171,88,.5);
  padding: 1em;
  color: #d9480f;
}
.button_wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 5px;
  grid-auto-rows: minmax(10px, auto);
}
.removeBtn {
  grid-column: 1 / 2;
  grid-row: 1;
}
.restoreBtn {
  grid-column: 1 / 4;
  grid-row: 2;
}
.archiveBtn {
  grid-column: 1 / 2;
  grid-row: 3;
}
.achieveBtn {
  
  grid-column: 1 / 4;
  grid-row: 2;
}
.editSwitchBtn {
  grid-column: 1 / 2;
  grid-row: 3;
}
.upBtn {
  grid-column: 3;
  grid-row: 1;	
}
.downBtn {
  grid-column: 3;
  grid-row: 3;	
}


.todoItem.achieved .achieveBtn{
  background-color: red;
}


.todoItem.achieved .id{
  background-color: red;
}


